
import { defineComponent } from "vue";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import type { DCM20100111_RES, DCM20100111_REQ, ChequeRequestList } from '@/shared/type/DCM20100111.type';
import { PAGE_SIZE } from "@/shared/constants/common.const";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
    name: "dcm201001sc00",
    components: {
        Pagination,
        BreadCrumb,
        SelectOption
    },
    data() {
        const chequeRequestList: ChequeRequestList[] = []
        const searchField = {
            receiverContactNo: "",
            checkBookProgressStatusCode: this.$route.query.filter || '',
            receiverName: "",
        }
        const header: { columnName: string }[] = [
            { columnName: this.$t('dcm201001sc00.column.no') },
            { columnName: this.$t('dcm201001sc00.column.action') },
            { columnName: this.$t('dcm201001sc00.column.request_date') },
            { columnName: this.$t('dcm201001sc00.column.recipient_name') },
            { columnName: this.$t('dcm201001sc00.column.recipient_phone_no') },
            { columnName: this.$t('dcm201001sc00.column.check_book_quantity') },
            { columnName: this.$t('dcm201001sc00.column.fee_amount') },
            { columnName: this.$t('dcm201001sc00.column.branch_to_visit') },
            { columnName: this.$t('dcm201001sc00.column.status') },
            { columnName: this.$t('dcm201001sc00.column.remark') },
            { columnName: this.$t('dcm201001sc00.column.update_date') },
            { columnName: this.$t('dcm201001sc00.column.update_by') },
        ]
        const totalItems = 0 as number
        const currentPage = 1 as number
        const pageSize = PAGE_SIZE as number
        const startingIndex = 1 as number
        const isResetPage = false as boolean

        return {
            chequeRequestList,
            header,
            searchField,
            totalItems,
            currentPage,
            pageSize,
            startingIndex,
            isResetPage
        };
    },

    mounted() {
        this.requestChequeRequestDetail();
    },

    computed: {
        Status() {
            return this.$store.state.CommonCodeModule.commonCode.Status
        },
        CheckBookProgressStatusCode() {
            return this.$store.state.CommonCodeModule.commonCode.CheckBookProgressStatusCode
        }
    },

    methods: {
        async requestChequeRequestDetail() {
            const reqBody = {
                checkBookProgressStatusCode: this.searchField.checkBookProgressStatusCode,
                receiverContactNo: this.searchField.receiverContactNo,
                receiverName: this.searchField.receiverName,
                pageSize: this.pageSize,
                pageNumber: this.currentPage,
            } as DCM20100111_REQ;
            const res = await this.$request.getRequest("DCM20100111", reqBody, false) as DCM20100111_RES;

            this.chequeRequestList = res.body.list.map((item: ChequeRequestList) => {
                return {
                    lastChangeDate: this.$datetime.formatStringToStringDate(item.lastChangeDate),
                    approvalRequestDate: this.$datetime.formatStringToStringDate(item.approvalRequestDate),
                    lastChangeTellerID: item.lastChangeDate,
                    lastChangeTellerName: item.lastChangeTellerName,
                    updateBy: item.lastChangeTellerName && item.lastChangeTellerID ? `${item.lastChangeTellerName} (${item.lastChangeTellerID})` : '',
                    feeAmount: this.$format.formatCurrency(item.feeAmount, item.currencyCode),
                    receiverContactNo: this.$format.formatPhoneNumber(item.receiverContactNo),
                    receiverName: item.receiverName,
                    internetBankingApprovalNo: item.internetBankingApprovalNo,
                    currencyCode: item.currencyCode,
                    visitBranchName: item.visitBranchName,
                    chequeQty: item.chequeQty,
                    chequeBookQty: item.chequeBookQty,
                    remark: item.remark,
                    checkBookProgressStatusCode: `${item.checkBookProgressStatusCode} : ${this.$codeConverter.convertValue("CheckBookProgressStatusCode", item.checkBookProgressStatusCode)}`,
                    // checkBookProgressStatusCode: this.$codeConverter.convertValue("CheckBookProgressStatusCode", item.checkBookProgressStatusCode)
                }
            })

            this.totalItems = res.body.totalCount;
        },

        handleRowClick(item: ChequeRequestList) {
            this.$router.push(
                {
                    path: `DCM201001SC01`,
                    query: {
                        internetBankingApprovalNo: item.internetBankingApprovalNo,
                        approvalRequestDate: this.$datetime.unFormatStringToStringDate(item.approvalRequestDate),
                    }
                });
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.currentPage = pageNumber;
            this.requestChequeRequestDetail();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case "search":
                    if (this.currentPage > 1) {
                        this.currentPage = 1
                        this.isResetPage = true;
                        this.requestChequeRequestDetail()
                    } else {
                        this.isResetPage = true;
                        this.requestChequeRequestDetail()
                    }
                    break;
                default: //reset
                    this.searchField.checkBookProgressStatusCode = ""
                    this.searchField.receiverContactNo = ""
                    this.searchField.receiverName = ""
                    this.currentPage = 1
                    this.isResetPage = true
                    this.requestChequeRequestDetail()
                    break;
            }
        },
    },
});
